<template>
	<div class="quotes" @click="mobileMenu = false">
    <div class="container">
      <div class="quotes-item" v-bind:title="subtitle">
        <transition name="component-fade" mode="out-in">
          <component v-bind:is="view"></component>
        </transition>
      </div>
      <input v-model="view" type="radio" value="v-a" id="a" name="view"><label for="a"></label>
      <input v-model="view" type="radio" value="v-b" id="b" name="view"><label for="b"></label>
      <input v-model="view" type="radio" value="v-g" id="g" name="view"><label for="g"></label>
    </div>
  </div>
</template>

<script>
	export default {
		name: 'quotes',
		data () {
			return {
				mobileMenu: false,
				view: 'v-a',
     		subtitle: 'Made by Vue.js'
			}
		},
		components: {
		    'v-a': {
		      template: '<div style="background-color: white;">It was some time before he obtained any answer, and the reply, when made, was unpropitious.</div>'
		    },
		    'v-b': {
		      template: '<div style="background-color: white;">Dolor sit amet, consectetur adipisicing elit. Alias pariatur rem debitis eaque officia provident, ab voluptatum, id omnis aliquam praesentium suscipit odio ut reiciendis autem voluptatem!</div>'
		    },
		    'v-g': {
		      template: '<div style="background-color: white;">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quae quasi debitis ut sit recusandae exercitationem excepturi modi iusto assumenda.</div>'
		    }
		}
	}
</script>

<style scoped>
.quotes {
  background-color: #f5f6f8;
  padding: 20px 0;
  text-align: center;
}
.quotes-item {
  padding: 35px 15px;
  margin: auto;
  width: 60%;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 100;
  font-size: 42px;
  color: black;
}
.quotes input {
  cursor: pointer;
  margin: 30px 15px 0 0;
}	
.component-fade-enter-active {
  transition: all .4s ease;
}
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to {
  opacity: 0;
}
@media only screen and (max-width : 992px) {
  .quotes-item {
    width: 100%;
    padding: 0;
    font-size: 40px;
  }
}
@media only screen and (max-width : 768px) {
  .quotes-item {
    font-size: 30px;
  }
}
</style>


